.hero {
  height: 908px;
  padding-top: 180px;
  background: url("../assets/pictures/hero/noise_transparent@2x.png"),
              radial-gradient(100% 215.42% at 0% 0%, #5B483A 0%, #262425 100%),
              // url("../assets//pictures/hero/start-screen-background.jpg") no-repeat top left,
              #211F20;

  &__container {
    display: flex;
    justify-content: space-between;
    height: 100%;
  }

  &__inner {
    padding-top: 163px;
    left: var(--container-padding);
    flex-basis: 38.33%;
  }

  &__dog {
    flex-basis: 58.16%;
    background: url("../assets/pictures/hero/start-screen-puppy.png") no-repeat bottom right;
  }

  &__title {
    font-family: var(--header-font);
    width: 310px;
    font-size: 44px;
    line-height: 57px;
    color: var(--color-light-xl);
    margin-bottom: 42px;
  }

  &__content {
    font-family: var(--main-font);
    font-size: 15px;
    line-height: 24px;
    color: var(--color-dark-s);
    margin-bottom: 42px;
  }

  &__button {
    display: inline-block;
  }
}